;define("service.adult-verify", ["jquery", "util", "config", "viewmodel", "service.modal"],
	function($, util, config, viewmodel, modal, undefined){
		var modalAPI
		,	fnOnModalResult
		,	fnDestroy
		,	fnUnbind
		,	vmAdultVerify
		,	mInfo = config.custInfo
		,	isNeed = (mInfo && parseInt(mInfo.adult_verify))? true : false
		,	isAdult = !isNeed
		,	cookieKey = "adult-verify-isAdult"
		,	iExpireHour = 6
		,	promise = util.promise()
		;

		fnOnModalResult = function(result, event){
			var vm = vmAdultVerify;

			if (result === "cancel"){
				promise.anyway(result, event);
				fnUnbind();

				return;
			}

			vmAdultVerify.promise()
			.done(function(result, msg){
				if (result === "new"){
					alert("인증 성공!");
				}
				else if (result === "exists"){
					alert("인증 성공.");
				}
				else{
					alert("인증 성공 : " + result);
				}

				util.cookie.set( cookieKey, "yes", iExpireHour );
				isAdult = true;
				modalAPI.hide();
				promise.resolve(result, msg);
				
			})
			.fail(function(result, msg){
				alert( msg );

				if (result === "tooYoung"){
					modalAPI.hide();
				}
				promise.reject(result, msg);
			});
		};

		fnUnbind = function(){
			modalAPI.unbindAll();
			promise.unbind();
		};

		fnDestroy = function(){
			if (modalAPI){
				fnUnbind();
				modalAPI.destroy();
				modalAPI = undefined;
			}
		};

		return function(opt){
			if (opt === "destroy"){
				fnDestroy();

				return;
			}

			if (isNeed){
				isAdult = util.cookie.get( cookieKey ) === "yes";
			}

			if (isAdult === true){
				promise.resolve("exists", "");
				//promise.immediate("done")();

				return promise;
			}

			if (!modalAPI){
				if (!opt || !opt.viewModel){
					vmAdultVerify = viewmodel("adultverify").init();
				}
				else{
					vmAdultVerify = viewmodel(opt.viewModel).init();;
				}
				modalAPI = modal( util.mapValDef( opt, "modal", "#modal_adultVerify" ) );
			}

			modalAPI.bind("result", fnOnModalResult)
			.clear()
			.show();

			return promise;
		};
	});