;define("service.player.desktop", ["jquery", "util", "service.weblauncher.desktop"], 
	function($, util, weblauncher){

		return function(info){
			var contentId 	= util.mapValDef(info, "content_id")
			;

			return {
				play: function(){
					if (!contentId){
						alert(
							"재생 할 영상 정보가 부족합니다.\n" +
							"CONTENT_ID=" + contentId
							);

						return;
					}

					weblauncher.play( info );
				},
				showTV: function(){
					weblauncher.showTV();
				}
			};
		};
	});